document.addEventListener("DOMContentLoaded", () => {
  const sections = document.querySelectorAll("section");

  document.querySelectorAll(".home-link").forEach((navLink) => {
    navLink.addEventListener("click", (event) => {
      event.preventDefault();
      const targetSectionId = event.target.getAttribute("href");
      const targetSection = document.querySelector(targetSectionId);

      sections.forEach((section) => {
        if (section === targetSection) {
          section.classList.remove("hidden");
          section.scrollIntoView({ behavior: "smooth" });
          setTimeout(() => {
            section.scrollIntoView({ behavior: "smooth" });
          }, 100);
        } else {
          section.classList.add("hidden");
        }
      });
    });
  });

  const navbar = document.querySelector(".navbar");
  const closeMenuButton = document.querySelector(".hamberg1");

  if (menuButton && navbar) {
    menuButton.addEventListener("click", () => {
      menuButton.style.visibility = "hidden";
      closeMenuButton.style.visibility = "visible";
      navbar.classList.toggle("activate");
    });
  }

  if (closeMenuButton) {
    closeMenuButton.addEventListener("click", () => {
      menuButton.style.visibility = "visible";
      closeMenuButton.style.visibility = "hidden";
      navbar.classList.toggle("activate");
    });
  }

  if (navbar) {
    navbar.addEventListener("click", (event) => {
      if (
        event.target.id === "I" ||
        (event.target.className === "home-link" && window.innerWidth <= 1524)
      ) {
        navbar.classList.toggle("activate");
        menuButton.style.visibility = "visible";
        closeMenuButton.style.visibility = "hidden";
      }
    });
  }

  const profileDetails = [
    { key: "Name:", value: "Prajwal&nbspP&nbspS" },
    { key: "Email:", value: "prajwalberpadka@gmail.com" },
    { key: "Phone:", value: "+91&nbsp8296080682" },
    { key: "City:", value: "Sullia, Karnataka" },
    { key: "Degree:", value: "Bachelor's" },
    { key: "Age:", value: "19" },
    { key: "Freelance:", value: "Available" },
  ];

  const profileContainer = document.getElementById("profile");
  const profileHeader = document.getElementById("profileHead");

  let currentLineIndex = 0;
  let currentKeyCharIndex = 0;
  let currentValueCharIndex = 0;

  profileHeader.innerHTML += '<span class="datatype">Const</span>';
  profileHeader.innerHTML += '<span class="var">Profile</span>';
  profileHeader.innerHTML += '<span class="equalto">=</span>';
  profileHeader.innerHTML += '<span class="specialchar">{</span>';

  function typeEffect() {
    if (currentLineIndex < profileDetails.length) {
      const currentLine = profileDetails[currentLineIndex];

      if (currentKeyCharIndex < currentLine.key.length) {
        profileContainer.innerHTML += `<span class="key">${currentLine.key[currentKeyCharIndex]}</span>`;
        currentKeyCharIndex++;

        setTimeout(typeEffect, 100);
      } else if (currentValueCharIndex < currentLine.value.length) {
        if (currentValueCharIndex === 0) {
          profileContainer.innerHTML += `<span class="specialchar">"</span>`;
        }
        if (currentLine.value[currentValueCharIndex] === "&") {
          profileContainer.innerHTML += "&nbsp;";
          currentValueCharIndex += 5;
        }
        profileContainer.innerHTML += ` <span class="value">${currentLine.value[currentValueCharIndex]}</span>`;

        currentValueCharIndex++;
        if (currentValueCharIndex === currentLine.value.length) {
          profileContainer.innerHTML += `<span class="specialchar">"</span>`;
        }
        setTimeout(typeEffect, 100);
      } else {
        profileContainer.innerHTML += '<span class="specialchar">,</span>';
        profileContainer.innerHTML += "<br>";
        currentLineIndex++;
        if (currentLineIndex === 7)
          profileContainer.innerHTML += '<div class="specialchar">};</div>';
        currentKeyCharIndex = 0;
        currentValueCharIndex = 0;

        setTimeout(typeEffect, 1000);
      }
    } else {
      setTimeout(() => {
        profileContainer.innerHTML = "";
        currentLineIndex = 0;
        typeEffect();
      }, 2000);
    }
  }

  // Start the typing effect
  typeEffect();
});

